import React from 'react';
import CurrentUserContext from '../contexts/currentUserContext'; // –ò–º–ø–æ—Ä—Ç–∏—Ä—É–µ–º –∫–æ–Ω—Ç–µ–∫—Å—Ç

export default function Card({ card, onCardClick, onCardLike, onCardDelete }) {

  // –ü–æ–¥–ø–∏—Å—ã–≤–∞–µ–º—Å—è –Ω–∞ –∫–æ–Ω—Ç–µ–∫—Å—Ç CurrentUserContext
  const currentUser = React.useContext(CurrentUserContext);

  // –û–ø—Ä–µ–¥–µ–ª—è–µ–º, —è–≤–ª—è–µ–º—Å—è –ª–∏ –º—ã –≤–ª–∞–¥–µ–ª—å—Ü–µ–º —Ç–µ–∫—É—â–µ–π –∫–∞—Ä—Ç–æ—á–∫–∏
  const isOwn = card.owner._id === currentUser._id;

  // –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –∫–æ—Ç–æ—Ä—É—é –ø–æ—Å–ª–µ –∑–∞–¥–∞–¥–∏–º –≤ `className` –¥–ª—è –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è
  const cardDeleteButtonClassName = (
    `card__delete-button ${isOwn ? 'card__delete-button_visible' : 'card__delete-button_hidden'}`
  );

  // –û–ø—Ä–µ–¥–µ–ª—è–µ–º, –µ—Å—Ç—å –ª–∏ —É –∫–∞—Ä—Ç–æ—á–∫–∏ –ª–∞–π–∫, –ø–æ—Å—Ç–∞–≤–ª–µ–Ω–Ω—ã–π —Ç–µ–∫—É—â–∏–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º
  const isLiked = card.likes.some(i => i._id === currentUser._id);

  // –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –∫–æ—Ç–æ—Ä—É—é –ø–æ—Å–ª–µ –∑–∞–¥–∞–¥–∏–º –≤ `className` –¥–ª—è –∫–Ω–æ–ø–∫–∏ –ª–∞–π–∫–∞
  let cardLikeButtonClassName = (
    `card__like-button ${isLiked ? 'card__like-button_active' : ''}`
  );

  function handleCardClick() {
    // –ü–µ—Ä–µ–¥–∞—ë–º –Ω–∞–∑–≤–∞–Ω–∏–µ –∏ —Å—Å—ã–ª–∫—É –Ω–∞–∂–∞—Ç–æ–π –∫–∞—Ä—Ç–∏–Ω–∫–∏ –≤ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç App –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è lightbox:
    onCardClick(card)
  }

  function handleLikeClick() {
    // –ü–µ—Ä–µ–¥–∞—ë–º –∫–∞—Ä—Ç–æ—á–∫—É –¥–ª—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –ª–∞–π–∫–∞:
    onCardLike(card)
  }

  function handleDeleteClick() {
    // –ü–µ—Ä–µ–¥–∞—ë–º –∫–∞—Ä—Ç–æ—á–∫—É –¥–ª—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –ª–∞–π–∫–∞:
    onCardDelete(card)
  }

  return(
    <li className="card" key={card.key}>
      <img className="card__image" src={card.link} alt={card.title} onClick={handleCardClick} />
      <button className={cardDeleteButtonClassName} type="button" aria-label="–£–¥–∞–ª–∏—Ç—å" onClick={handleDeleteClick}></button>
      <div className="card__content">
        <h2 className="card__title">{card.name}</h2>
        <div>
        <button className={cardLikeButtonClassName} type="button" aria-label="–ù—Ä–∞–≤–∏—Ç—Å—è" onClick={handleLikeClick}></button>
          <p className="card__likes">{card.likes.length}</p>
        </div>
      </div>
    </li>
  )
}
