class Api {
// getCards(): –ø–æ–ª—É—á–∏—Ç—å —Å–ø–∏—Å–æ–∫ –≤—Å–µ—Ö –∫–∞—Ä—Ç–æ—á–µ–∫ —Å —Å–µ—Ä–≤–µ—Ä–∞ –≤ –≤–∏–¥–µ –º–∞—Å—Å–∏–≤–∞ (GET)
// getUser(): –ø–æ–ª—É—á–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ  (GET)
// setUser(): –∑–∞–º–µ–Ω–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ (PATCH)
// uploadCard(): –¥–æ–±–∞–≤–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É –Ω–∞ —Å–µ—Ä–≤–µ—Ä (POST)
// deleteCard(): —É–¥–∞–ª–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É (DELETE)
// setAvatar(): –∑–∞–º–µ–Ω–∏—Ç—å –∞–≤–∞—Ç–∞—Ä (PATCH)
// setLike(): –∑–∞–ª–∞–π–∫–∞—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É (PUT)
// removeLike(): —É–¥–∞–ª–∏—Ç—å –ª–∞–π–∫ –∫–∞—Ä—Ç–æ—á–∫–∏ (DELETE)

  constructor({baseUrl, headers}) {
    this._baseUrl = baseUrl;
    this._headers = headers;
  }

  _fetchButCatch(url, init) {
    return fetch(url, init)
    .then((response) => {
      if (response.ok) {
        return response.json()
      }
      return Promise.reject(new Error(`${response.status}`))
    })
  }

  getUser() {
    return this._fetchButCatch(`${this._baseUrl}/users/me`, {
      headers: this._headers
    })
  }

  setUser(userData) {
    return this._fetchButCatch(`${this._baseUrl}/users/me`, {
      method: 'PATCH',
      headers: this._headers,
      body: JSON.stringify({
        name: userData.name,
        about: userData.about
      })
    });
  }

  setAvatar(imgSrc) {
    return this._fetchButCatch(`${this._baseUrl}/users/me/avatar`, {
      method: 'PATCH',
      headers: this._headers,
      body: JSON.stringify({
        avatar: imgSrc
      })
    });
  }

  uploadCard(card) {
    return this._fetchButCatch(`${this._baseUrl}/cards`, {
      method: 'POST',
      headers: this._headers,
      body: JSON.stringify({
        name: card.name,
        link: card.link
      })
    });
  }

  getCards() {
    return this._fetchButCatch(`${this._baseUrl}/cards`, {
      headers: this._headers
    })
  }

  deleteCard(card) {
    return this._fetchButCatch(`${this._baseUrl}/cards/${card._id}`, {
      method: 'DELETE',
      headers: this._headers
    });
  }

  changeLikeCardStatus(card, isLiked) {
    if (isLiked) {
      return this.removeLike(card)
    }
    else {
      return this.setLike(card)
    }
  }

  setLike(card) {
    return this._fetchButCatch(`${this._baseUrl}/cards/likes/${card._id}`, {
      method: 'PUT',
      headers: this._headers
    });
  }

  removeLike(card) {
    return this._fetchButCatch(`${this._baseUrl}/cards/likes/${card._id}`, {
      method: 'DELETE',
      headers: this._headers
    });
  }

}

const api = new Api({
  baseUrl: 'https://mesto.nomoreparties.co/v1/cohort-16',
  headers: {
    authorization: '25068d5b-79ef-423f-8b22-b9922c31ad6c',
    'Content-Type': 'application/json'
  }
});

export default api
